<template>
    <div>
        <h1>Feedback</h1>

        <div class="container">
            <div class="row">
                <div class="col-12">
                    <div class="card">
                        <div class="row my-3">
                            <div class="col-12 d-flex justify-content-center ">
                                <router-link :to="`/feedback/create`">
                                    <div class="large-add-button">
                                        <span>+</span>
                                    </div>
                                </router-link>
                            </div>
                        </div>

                        <div class="row">
                            <div class="col-12">
                                <div class="card-header">
                                    <div class="card-header-text">Vragen</div>
                                </div>

                                <div class="card-body">
                                 <div :key="question.question" v-for="question in questions">{{ question.question}}</div>
                                </div>
                            </div>
                        </div>


                    </div>
                </div>
            </div>
        </div>


    </div>
</template>

<script>
    import http from "../../http/http";

    export default {
        name   : "Index",
        data() {
            return {
                questions: []
            }
        },
        methods: {
            getQuestions() {
                http.get("/feedback/questions").then(response => {
                  this.questions = response.data;
                })
            }
        },


        created() {
            this.getQuestions();
        }
    }
</script>

